.header{background-color:#fff;box-shadow:0 2px 4px rgba(0,0,0,.1);padding:1rem 0;position:sticky;top:0;z-index:1000}.container{max-width:1200px;margin:0 auto;padding:0 1rem;display:flex;justify-content:space-between;align-items:center}.logo{font-size:1.5rem;font-weight:bold;color:#1a1a1a;text-decoration:none;transition:color .3s ease}.logo:hover{color:#0070f3}.nav{display:flex;gap:2rem;align-items:center}.link{color:#666;text-decoration:none;font-weight:500;transition:all .3s ease;padding:.5rem 0;position:relative}.link:hover{color:#0070f3}.link::after{content:"";position:absolute;bottom:0;left:0;width:0;height:2px;background-color:#0070f3;transition:width .3s ease}.link:hover::after{width:100%}